import styles from './ReportCard.module.css';

const ReportCard = (props) => {
    return (
        <div className={styles.card}>
            <div className={styles.person}>
                <img src='./imgs/image-jeremy.png' alt='jermy robson' />
                <p>Report for</p>
                <h2>Jeremy Robson</h2>
            </div>
            <div className={styles.interval}>
                <a 
                    href='#daily'
                    className={props.activeIndex === 1 ? styles.active : ''}
                    onClick={props.change}
                    id='daily'>
                    Daily
                </a>
                <a 
                    href='#weekly'
                    className={props.activeIndex === 2 ? styles.active : ''}
                    onClick={props.change}
                    id='weekly'>
                    Weekly
                </a>
                <a 
                    href='#monthly'
                    className={props.activeIndex === 3 ? styles.active : ''}
                    onClick={props.change}
                    id='monthly'>
                    Monthly
                </a>
            </div>
        </div>
    );
}

export default ReportCard;