import styles from './StatsCard.module.css';

const StatsCard = (props) => {
    return (
        <div className={`${styles.stats} ${styles[props.color]}`}>
            <div className={`${styles.background} ${styles[props.color]}`}>
                <img src={`../imgs/${props.img}.svg`} alt={props.img} />
            </div>
            <div className={styles.info}>
                <div className={styles.header}>
                    <p className={styles.title}>{props.title}</p>
                    <div className={styles.dots}>
                        <svg width="26" height="26" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20" fill="currentColor" color="hsl(236, 100%, 87%)"><path d="M6 10a2 2 0 11-4 0 2 2 0 014 0zm6 0a2 2 0 11-4 0 2 2 0 014 0zm4 2a2 2 0 100-4 2 2 0 000 4z"></path></svg>
                    </div>
                </div>
                <p className={styles.time}>{
                        props.hrs === 1 ?
                        props.hrs + 'hr' :
                        props.hrs + 'hrs'
                    }
                </p>
                <p className={styles.conclusion}>Yesterday - {
                        props.yesterday === 1 ?
                        props.yesterday + 'hr' :
                        props.yesterday + 'hrs'
                    }
                </p>
            </div>
        </div>
    );
}

export default StatsCard;